import React from 'react';
import styles from './App.module.scss'
import Header from './Header';
import {Switch, Route} from 'react-router-dom';
import Main from '../pages/MainComponents/Main';
import Operatives from '../pages/OperativesComponents/Operatives';
import LearnReact from '../pages/LearnReact/LearnReact'
function App(props) {
  return (
    <div className={styles.Layout}>
      <Header />
      <div className={styles.container}>
        <Switch>
          <Route exact path="/" component={Main}/>
          <Route path="/main" component={Main}/>
          <Route path="/operatives" component={Operatives}/>
          <Route path="/learnR" component={LearnReact}/>
        </Switch>
      </div>
    </div>
  );
}

export default App;
